import { Component, OnInit } from '@angular/core';
import { MainService } from './services/main.service';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  
  tweets = [];
  title = 'twitterOnu';
  height = window.innerHeight;
  tags = [];
  start_page = true;
  constructor(
    private mainService: MainService
  ) { }


  ngOnInit() {
    this.height = window.innerHeight;
    this.mainService.get_words().subscribe(data => {
      var tweets: [] = data;
      if (tweets.length == 0) {
        this.start_page = true;
      }
    })
  }

  is_start_page() {
    var ret = false;
    this.mainService.get_start_page().subscribe(data => {
      ret = data == undefined? false : data == null ? false : data;
    }, err => {
      ret = false;
    });
    return ret;
  }

  get_tweets(){
    var ret = [];
    this.mainService.get_tweets().subscribe(data => {
      ret = data == undefined? [] : data == null ? [] : data.tweets == undefined ? [] : data.tweets == null ? [] : data.tweets.length == 0? [] : data.tweets;
    }, err => {
      ret = [];
    });
    return ret;
  }
}
