import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { interval } from 'rxjs'
import * as d3 from 'd3';
declare const index: any;

@Component({
  selector: 'app-statics',
  templateUrl: './statics.component.html',
  styleUrls: ['./statics.component.css']
})
export class StaticsComponent implements OnInit {
  @ViewChild('graph_nodes') graph_container: ElementRef;
  height: any;
  width: any;
  constructor() { }

  ngOnInit() {
    this.height = window.innerHeight;
    this.width = this.graph_container.nativeElement.offsetWidth;
    index(this.width, this.height);

    const secondsCounter2 = interval(100);
    secondsCounter2.subscribe(n => {
      var p_w = this.graph_container.nativeElement.offsetWidth;
      if (this.width != p_w) {
        this.width = p_w;
        index(this.width, this.height);
      }
    });

  }

  click() {
    console.log(this.height);
    console.log(this.width);
    index(this.width, this.height);
  }

}
