import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { of } from 'rxjs'

import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { timeout } from 'rxjs/operators';


import { AppSettings } from '../app.settings';

const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json'
  })
}

@Injectable()
export class MainService {
  tweets: any;
  words = [];
  request = {};
  response = {};
  is_start_page = false;
  error;
  constructor(private http: HttpClient) { }

  get_words(): Observable<any> {
    return of(this.words);
  }

  set_words(words) {
    this.words = words;
  }

  load_tweets(req, count) {
    console.log(req)
    return this.http.get<any>(AppSettings.API_ENDPOINT + "tweets?keywords=" + req + '&count=' + count, httpOptions).pipe(timeout(180000));

  }

  get_tweets(): Observable<any> {
    return of(this.tweets);
  }


  set_tweets(tweets) {
    this.tweets = tweets;
  }

  get_start_page(): Observable<any> {
    return of(this.is_start_page);
  }

  set_start_page() {
    this.is_start_page = true;;
  }
}
