import { Component, OnInit } from '@angular/core';
import {MainService} from '../services/main.service';

@Component({
  selector: 'app-tweet',
  templateUrl: './tweet.component.html',
  styleUrls: ['./tweet.component.css']
})
export class TweetComponent implements OnInit {

  tweets = [];
  at: string = "@";
  hashtag: string = "#"
  constructor(private mainService : MainService) { }

  ngOnInit() {
    console.log('entró');
  }

  public highlight(text) {
    return text.replace(new RegExp("(@|#)(\\w*\\w*)", "gi"), match => {
      return '<span class="highlightText">' + match + '</span>'
    });
  }

  get_tweets(){
    var ret = [];
    this.mainService.get_tweets().subscribe(data => {
      ret =  (data.tweets != undefined && data.tweets != null)?data.tweets: [];
    }, err =>{
      ret = [];
    });
    return ret;
  }

  percent_tweet(tweet){
    return Math.round(Math.abs(tweet * 100));
  }


   class(tweet){
     return tweet == 'NEGATIVE' ? 'progress-bar bg-neg' : 'progress-bar bg-pos';
   }
}
